.side-nav{
	padding:20px;
	background:var(--color-one);
	box-shadow:1px 0px 7px rgba(0,0,0,0.5);
	position:fixed;
	right:0;
	z-index:199;
	transition: transform 0.5s, ease-out;
	align-self: center;
	text-align: center;
}
.hide{
	transform:translateX(+100%);
}
#nav-link i{
  font-size:30px;
}
.bar{
	position:fixed;
	z-index:200;
	top:10px;
	right:20px;
}
.bar1,.bar2,.bar3{
  width: 34px;
  height: 4px;
  background-color:var(--color-three);
  margin: 8px 0;
  border-bottom: solid 2px var(--color-one);
}

.change1{
  -webkit-transform: rotate(-50deg) translate(-9px, 8px);
  transform: rotate(-50deg) translate(-9px, 8px);
  transition: transform 0.6s, ease-out;
}

.change2 {opacity: 0;}

.change3 {
  -webkit-transform: rotate(50deg) translate(-9px, -8px);
  transform: rotate(50deg) translate(-9px, -8px);
  transition: transform 0.6s, ease-out;
}
